document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}

// Función para mantener la barra de navegación fija
function navegacionFija(){
    const barra = document.querySelector('.header');                    // Seleccionamos el elemento html que tenga la clase .header
    const sobreFestival = document.querySelector('.sobre-festival');    // Seleccionamos el elemento html que tenga la clase .sobre-festival
    const body = document.querySelector('body');

    window.addEventListener('scroll', function () {                     // Agregamos un evento scroll
        //console.log(sobreFestival.getBoundingClientRect() );          // con getBoundingClientRect obtenemos valores del scroll

        if( sobreFestival.getBoundingClientRect().top < 0){             // Si sobrepasamos el elemnto html con la clase .sobre-festival agreamos la clase .fijo (archivo _header.scss).
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        }
        else{
            barra.classList.remove('fijo');                             // Si no la pasamos, quitamos la clase .fijo (archivo _header.scss).
            body.classList.remove('body-scroll');
        }
    });
}

// Funcion para hacer scroll en el momento de hacer click en la barra de navegación
function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a');   // Seleccionamos los enlaces que tengan como padre la clase .navegacion-principal
    enlaces.forEach( enlaces => {                                           // Ciclo forEach 

        enlaces.addEventListener('click', function(e){                      // Evento click para cuando demos click en los enlaces. Siempre estamos recorriendo con el loop cada uno de los enlaces.
            e.preventDefault();                                             // Prevenimos la acción por defecto (es decir, que al momento de dar click en un enlace no nos lleve de golpe)
            const seccionScroll = e.target.attributes.href.value;           // Obtenemos con el evento e el valor del href del enlace clickeado
            const seccion = document.querySelector(seccionScroll);          // Obtenemos la sección clickeada
            seccion.scrollIntoView({ behavior: "smooth"});                  // Con scrollIntoView agregamos otro comportamiento. smooth permite ir a la sección de forma lenta y no de golpe.
        });
    });
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');

    for(let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/${i}.jpg" 
            alt="imagen galeria">
        `;
        //Para pasar parámetros tenemos que hacer callback. Si pasamos directo
        // imagen.onclick = mostrarimagen(i) se manda llamar la función dependiendo
        // el número del ciclo, en este caso 12.
        imagen.onclick = function () {
            mostrarImagen(i);
        }

        galeria.appendChild(imagen);
    }
}

function mostrarImagen(id){
    const imagen = document.createElement('picture');
    imagen.innerHTML = `
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" 
        alt="imagen galeria">
    `;

    //Creamos un elemento div. Crea el overlay con la imagen
    const overlay = document.createElement('DIV');
    //Agregamos la imágen al overlay
    overlay.appendChild(imagen);
    //Agreamos una clase a overlay llamada overlay
    overlay.classList.add('overlay');
    //Remover clase fijar-body al dar click en el overlay 
    //(es decir, dando click fuera de la imágen, no solamente en la X)
    overlay.onclick = function () {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body');
        overlay.remove();
    }    

    //Botón para cerrar el Modal(ventana modal)
    const cerrarModal = document.createElement('P');
    //Agregamos contenido al parrafo (cerrarModal)
    cerrarModal.textContent = 'X';
    //Agregamos una clase btn-cerrar al parrafo (cerrarModal)
    cerrarModal.classList.add('btn-cerrar');
    //Agregámos el párrafo a el div (overlay)
    overlay.appendChild(cerrarModal);

    //Creamos una constante body que selecciona el body del html    
    const body = document.querySelector('body');
    //Agregamos el overlay al body
    body.appendChild(overlay);
    //Agregamos una clase al body para posteriormente bloquear el scroll
    body.classList.add('fijar-body');

    //Evento onclick para remover el overlay
     cerrarModal.onclick = function() {
        const body = document.querySelector('body');
        //removemos la clase al body para volver a tener scroll
        body.classList.remove('fijar-body');
        overlay.remove();
    };
}